.container{
	min-height: 780px;
    height: 100%;
}
.forgetPwd-layout{
	text-align: center;
	width: 100%;
	max-width: 350px;
	margin: 0 auto;
}
.forgetPwd-layout .title{
	padding-top: 50%;
	margin-bottom: 19px;
}
.forgetPwd-layout input[type=text],
.forgetPwd-layout input[type=password]{
	margin-bottom: 19px;
	max-width: 324px;
}
.forgetPwd-layout #account{
	margin: 27px auto 14px;
}
.forgetPwd-layout  .btn{
	font: 18px "Microsoft Yahei";
	color: #ffffff;
	padding-top: 11px;
	padding-bottom: 11px;
}

.forgetPwd-layout #getKey{
	color: #6d6d6d;
	background-color: #dfdfdf;
	font-size: 18px;
	height: 45px;
}
.danger-border{
	border-color:#ff4e00;
}

.icon_error{
	width: 14px;
	height: 14px;
	margin: -1px 5px 0 0;
	overflow: hidden;
	display: inline-block;
	vertical-align: middle;
	background-image: url(data:image/png;base64,);
	background-repeat: no-repeat;
}
